import React, { Component } from 'react';
import hero from '../assets/img/hero.png';
import logo from '../assets/img/logo--white.png';

const style = {
  heroBackground: {
    backgroundImage: "url(" + hero + ")",
    backgroundPosition: 'center center',
    backgroundRepeat: 'no-repeat',
    backgroundSize: 'cover'
  },
  header: {
    background:"linear-gradient(to bottom, rgba(0, 0, 0, 0.2),rgba(0, 0, 0, 0.025))"
  },
  cta: {
    width: "100%"
  },
  only: {
    color: '#86d0f7'
  },
  small: {
    textShadow: "0px 0px 1px black",
    background:"rgba(0, 0, 0, 0.05)"
  },
  card: {
    background:"linear-gradient(to right, rgba(0, 0, 0, 0),rgba(0, 0, 0, 0.8))"
  },
  step: {
    background: "rgba(22, 45, 76, 0.6)"
  },
  or: {
    color: '#86d0f7'
  },
  number: {
    fontSize: "5rem"
  },
  stepTitle: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    marginBottom: 0
  },
  quantity: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center'
  },
  numberUSD: {
    fontSize: "5rem"
  }
}

class Hero extends Component {
  render() {
    return (
      <section className="hero is-fullheight" style={style.heroBackground}>
        <header className="hero-head" style={style.header}>
          <nav className="navbar is-transparent level" style={{ backgroundColor: 'transparent', textShadow: "1px 1px 1px black" }}>
            <div className="navbar-brand level-item">
              <a className="navbar-item" href="https://globatalent.com">
                <img src={logo} style={{ filter: 'drop-shadow(0px 0px 2px black)', maxHeight: '3rem' }} alt="Globatalent: The first sports crypto exchange where you can trade and support your favorite clubs and sports idols" />
              </a>
              <div role="button" className="navbar-burger" aria-label="menu" aria-expanded="false" data-target="navbarBurguer">
                <span></span>
                <span></span>
                <span></span>
              </div>
            </div>
            <div className="level-item"></div>
            <div className="level-item"></div>
            <div className="level-item"></div>
            <div id="navbarBurguer" className="navbar-menu level-item">
              <a className="navbar-item has-text-white" href="#about">
                {this.props.t('nav.url_1')}
              </a>
              <a className="navbar-item has-text-white" href="#contact">
                {this.props.t('nav.url_2')}
              </a>
            </div>
          </nav>
        </header>

        <section className="hero-body columns is-centered">
          <div className="column is-7"></div>
          
          <div className="column is-5">
            <article className="card columns is-uppercase" style={style.card}>
            <div className="column is-3"></div>
            <div className="column is-9">
              <div className="card-content">
                <div className="title has-text-centered is-1 has-text-white">
                  {this.props.t('cta.title')}
                  <div className="is-size-3" style={style.only}>{this.props.t('cta.only')}</div>
                </div>
                <div className="columns" style={{alignItems:"center"}}>
                <div className="column is-6 is-size-1 has-text-white has-text-centered has-text-weight-bold" style={{marginTop: "-2rem"}}><span style={{fontSize:"4rem"}}>{this.props.t('cta.quantity')}</span> <span className="is-size-2">{this.props.t('cta.currency')}</span></div>
                <div className="column is-size-4 has-text-centered has-text-white has-text-weight-bold" style={{marginTop: "-2rem"}}>{this.props.t('cta.action')}</div>
                </div>
                <div className="is-size-5 has-text-white has-text-centered has-text-weight-bold" style={{marginTop: "-1rem"}}>
                {this.props.t('cta.result')}
                </div>
              </div>

              <footer className="card-footer" style={{ borderTop: "0", marginTop: "-1rem" }}>
                <a href={"https://market.globatalent.com/register/?" + this.props.affiliateId} style={{ backgroundColor: '#ffa200' }} className="button is-fullwidth is-medium has-text-white">
                  {this.props.t('cta.button')}
                </a>
              </footer>
              </div>
            </article>
          </div>
          
        </section>

        <footer className="hero-foot">
        <small className="has-text-white-ter is-size-7 has-text-weight-light" style={style.small}>{this.props.t('info.small')}</small>
          <div className="tile is-ancestor has-text-centered">
            <div className="tile is-parent">
              <div className="tile is-child box has-text-white" style={style.step}>
                <p className="title has-text-white is-uppercase" style={style.stepTitle}><span style={style.number}>1</span><span className="is-size-3">{this.props.t('steps.step1.action')}</span></p>
                <p>{this.props.t('steps.step1.body')}</p>
              </div>
            </div>


            <div className="tile is-parent">
              <div className="tile is-child box has-text-white" style={style.step}>
                <p className="title has-text-white is-uppercase" style={style.stepTitle}><span style={style.number}>2</span><span className="is-size-3">{this.props.t('steps.step2.action')}</span></p>
                <p>{this.props.t('steps.step2.body')}</p>
              </div>
            </div>


            <div className="tile is-parent">
              <div className="tile is-child box has-text-white" style={style.step}>
              <p className="title has-text-white is-uppercase" style={style.stepTitle}><span style={style.number}>3</span><span className="is-size-3">{this.props.t('steps.step3.action')}</span></p>
                <p className="has-text-centered"><span className="has-text-weight-bold" style={style.or}>{this.props.t('steps.step3.first.blue')}</span> {this.props.t('steps.step3.first.white')}</p>
                <div className="has-text-centered is-size-6" style={style.or}>{this.props.t('steps.step3.second')}</div>
                <p className="has-text-centered"><span className="has-text-weight-bold" style={style.or}>{this.props.t('steps.step3.third.blue')}</span> {this.props.t('steps.step3.third.white')}</p>
              </div>
            </div>
          </div>
        </footer>

      </section>
    )
  }
}

export default Hero;